import React from 'react';
import styles from './RankTop.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

const RankTop = ({RankInfo}) => {
    const TopRank = RankInfo.filter(rankinfo => rankinfo.Rank <= 5 );
    console.log(TopRank);
    return (
        <ul className={cx('TopRank')}>
            <li className={cx('First','high-rank', 'big')}>
                <div className={cx('Top_Rank')}>{TopRank[0].Rank}</div>
                <div className={cx('Top_Name')}>{TopRank[0].Name}</div>
                <div className={cx('Top_Point')}>{TopRank[0].Point} Yam</div>
                <div className={cx('Top_Level')}>{TopRank[0].level} Level</div>
                <div className={cx('Top_Winrate')}>{TopRank[0].winrate}</div>
            </li>
            <li className={cx('Second','high-rank', 'small')}>
                <div className={cx('Top_Rank')}>{TopRank[1].Rank}</div>
                <div className={cx('Top_Name')}>{TopRank[1].Name}</div>
                <div className={cx('Top_Point')}>{TopRank[1].Point} Yam</div>
                <div className={cx('Top_Level')}>{TopRank[1].level} Level</div>
                <div className={cx('Top_Winrate')}>{TopRank[1].winrate}</div>
            </li>
            <li className={cx('Third','high-rank', 'small')}>
                <div className={cx('Top_Rank')}>{TopRank[2].Rank}</div>
                <div className={cx('Top_Name')}>{TopRank[2].Name}</div>
                <div className={cx('Top_Point')}>{TopRank[2].Point} Yam</div>
                <div className={cx('Top_Level')}>{TopRank[2].level} Level</div>
                <div className={cx('Top_Winrate')}>{TopRank[2].winrate}</div>
            </li>
            <li className={cx('Second','high-rank', 'small')}>
                <div className={cx('Top_Rank')}>{TopRank[3].Rank}</div>
                <div className={cx('Top_Name')}>{TopRank[3].Name}</div>
                <div className={cx('Top_Point')}>{TopRank[3].Point} Yam</div>
                <div className={cx('Top_Level')}>{TopRank[3].level} Level</div>
                <div className={cx('Top_Winrate')}>{TopRank[3].winrate}</div>
            </li>
            <li className={cx('Second','high-rank', 'small')}>
                <div className={cx('Top_Rank')}>{TopRank[4].Rank}</div>
                <div className={cx('Top_Name')}>{TopRank[4].Name}</div>
                <div className={cx('Top_Point')}>{TopRank[4].Point} Yam</div>
                <div className={cx('Top_Level')}>{TopRank[4].level} Level</div>
                <div className={cx('Top_Winrate')}>{TopRank[4].winrate}</div>
            </li>
        </ul>
    );
};

export default RankTop;