import React from 'react';
import styles from './RankList.scss';
import classNames from 'classnames/bind';

import RankItem from 'component/Rank/RankItem';

const cx = classNames.bind(styles);

const Rank = ({ RankInfo }) => {
    return (
        <React.Fragment>
            {
                RankInfo.map(
                    (info,index) => (
                        <RankItem
                            rank={info.Rank}
                            name={info.Name}
                            point={info.Point}
                            level={info.level}
                            winrate={info.winrate}
                            key={index}
                        />
                    )
                )
            }
        </React.Fragment>
    );
};

export default Rank;